body {
  background-color: rgb(240, 235, 235);
  font-family: Arial, Helvetica, sans-serif;
  color: #fff;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.undefined {
  background-color: rgb(194, 200, 202);
  width: 100%;
  height: 100%;
  border-radius: 6px;
  margin: auto;
  transition: 0.4s ease;
}

.app {
  background-color: rgb(40, 114, 148);
  width: 100%;
  height: 100%;
  border-radius: 6px;
  margin: auto;
  transition: 0.4s ease;
}

.app-warm {
  background-color: rgb(236, 203, 91);
  width: 100%;
  height: 100%;
  border-radius: 6px;
  margin: auto;
  transition: 0.4s ease;
}

main {
  min-height: 100vh;
  padding: 25px;
}
.search-box {
  width: 90%;
  margin: auto;
  border-radius: 6px;
  box-shadow: 0px 5px rgba(0, 0, 0, 0.5);
}

.search-bar {
  display: block;
  width: 100%;
  padding: 15px;
  appearance: none;
  background: none;
  border: none;
  outline: none;
  color: #fff;
  background-color: rgb(145, 154, 154);
  border-radius: 6px;
  margin-top: -25px;
  transition: 0.4s ease;
}

.search-bar:focus {
  background-color: rgb(104, 108, 113);
}

.location-box .location {
  font-weight: 800;
  font-size: 32px;
  text-align: center;
}

.location-box .date {
  font-style: italic;
  text-align: center;
}

.weather {
  text-align: center;
}

.weather-box,
.temp {
  text-align: center;
  margin: 20px auto;
}

.temp {
  font-size: 102px;
}

.weather {
  font-size: 42px;
  font-weight: 800;
}
